import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import Botao from '../../components/Botao/Botao';
import Header from '../../components/Header/Header';
import Modal from '../../components/Modal/Modal';
import TabMenu from '../../components/TabMenu/TabMenu';
import './Vaga.css'

function Vaga() {

    const [modalOpen, setModalOpen] = useState(false)
    const [vaga, setVaga] = useState({
        idVaga: 0,
        imgEmpresa: '',
        empresa: '',
        dtEncerramento: '',
        salario: 0,
        cargo: '',
        descricao: '',
        hardSkills: [],
        match: 0
    })

    useEffect(() => {
        let loadVaga = {
            idVaga: 5,
            imgEmpresa: 'data:image/png;base64,',
            empresa: 'Colgio Modulo',
            dtEncerramento: '17/10/2022',
            salario: 2300.50,
            cargo: 'Estgio em desenvolvimento',
            descricao: 'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Amet a sit doloremque quae culpa, tempore explicabo blanditiis praesentium nisi natus pariatur, maiores adipisci deserunt dolore beatae? Similique deserunt error delectus! Lorem ipsum dolor, sit amet consectetur adipisicing elit. Amet a sit doloremque quae culpa, tempore explicabo blanditiis praesentium nisi natus pariatur, maiores adipisci deserunt dolore beatae? Similique deserunt error delectus!',
            hardSkills: ['Java', 'Oracle DB', 'HTML', 'CSS3', 'JavaScript'],
            vagas: 2,
            match: 70
        }

        setVaga(loadVaga)
    }, [])

    function corMatch(match) {
        if (match <= 40) {
            return 'var(--barra3)'

        } else if (match <= 70) {
            return 'var(--barra2)'

        } else {
            return 'var(--barra1)'
        }
    }

    return (
        <>
            <Header />

            <main className="container">
                <div className="vaga">

                    <div>
                        <img src={vaga.imgEmpresa} alt="Logo" />
                        <h1>{vaga.empresa}</h1>
                    </div>

                    <p>
                        <span>Match Level: </span>
                        <strong style={{ backgroundColor: corMatch(vaga.match) }}>
                            {vaga.match}%
                        </strong>
                    </p>

                    <p>
                        <span>Inscrio at: </span> {vaga.dtEncerramento}
                    </p>

                    <p>
                        <span>Salrio: </span> R${vaga.salario.toFixed(2)}
                    </p>

                    <p>
                        <span>Cargo: </span> {vaga.cargo}
                    </p>

                    <p>
                        <span>Hard Skills: </span>
                        {
                            vaga.hardSkills.map((skill, index) => {
                                let aux = skill + ', '
                                if (index + 1 === vaga.hardSkills.length)
                                    aux = aux.replace(',', '')

                                return aux
                            })
                        }
                    </p>

                    <p>
                        <span>Vagas Dsponiveis: </span>{vaga.vagas}
                    </p>

                    <p>
                        <span>Descrio: </span>{vaga.descricao}

                    </p>

                    <div className="botoes">
                        <Link to="/vagas">
                            <Botao tipo='vazio'>Voltar</Botao>
                        </Link>
                        <Botao tipo='cheio' cor='azul' onClick={() => setModalOpen(true)}>
                            Me Candidatar
                        </Botao>

                        <Modal isOpen={modalOpen} setOpen={setModalOpen} titulo="Sucesso">
                            <i className="fi fi-check-mark-circle candidatura-realizada-check"></i>
                            <span>Candidaduta realizada!</span>
                        </Modal>
                    </div>

                </div>
            </main>

            <TabMenu />
        </>
    )
}

export default Vaga;